.video-time-control {
  width: 100%;
  height: 27px;
  display: flex;
  overflow: hidden;
  border-top: 2px solid black;
  border-bottom: 2px solid black;
  border-color: black;
  border: 4px;
  justify-content: center;
  padding-top: 2px;
  padding-bottom: 2px;
  background: white;
}
.video-time-control .left-control-panel {
  width: 100%;
  height: 100%;
}
.video-time-control .left-control-panel .time-line {
  width: 100%;
  height: 50%;
  background-color: white;
}
.video-time-control .left-control-panel .time-seek-bar {
  width: 100%;
  height: 50%;
  display: flex;
  align-items: center;
  background-color: white;
  padding-left: 12px;
}
.video-time-control .left-control-panel .time-seek-bar .all-seek-bar-slide {
  height: 22px;
  width: 100%;
  display: flex;
  background-color: #f6f6f7;
  position: relative;
}
.video-time-control .left-control-panel .time-seek-bar .all-seek-bar-slide .per-seek-bar {
  height: 22px;
  display: flex;
  background-color: #f6f6f7;
}
.video-time-control .left-control-panel .time-seek-bar .all-seek-bar-slide .per-seek-bar .left-bar-undrag {
  height: 100%;
  border-radius: 4px;
  padding-left: 5px;
  font-family: NotoSansHans-Regular;
  font-size: 12px;
  color: #000000;
  display: flex;
  align-items: center;
  font-weight: bold;
}
.video-time-control .left-control-panel .time-seek-bar .all-seek-bar-slide .per-seek-bar .left-bar-drag {
  border-radius: 4px;
  display: flex;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  align-items: center;
  justify-content: center;
  height: 22px;
}
.video-time-control .left-control-panel .time-seek-bar .all-seek-bar-slide .per-seek-bar .left-bar-drag .drag-line-box {
  background: rgba(0, 0, 0, 0.4);
  width: 10px;
  height: 22px;
  display: flex;
  justify-content: flex-end;
  cursor: ew-resize;
}
.video-time-control .left-control-panel .time-seek-bar .all-seek-bar-slide .per-seek-bar .left-bar-drag .drag-line-box .drag-line {
  width: 3px;
  height: 22px;
